.block-card {
  width: 300px;
  height: 230px;
  border: 1px solid white;
  border-radius: 20px;
  padding: 20px;
}
.block-card:hover {
  cursor: pointer;
}
.block-card .block-images {
  height: 90%;
}
.block-card .block-header {
  flex: 1;
}
.block-card h1 {
  font-size: 20px;
}
